import React from 'react';

import './Topbar.css';

// IMPORT ICONS
import searchIco from '../../../assets/icons/search.svg';
import bellIco from '../../../assets/icons/bell.svg';
import downIco from '../../../assets/icons/chevron-down.svg';

// IMPORT JQUERY
import $ from 'jquery';

function Topbar() {

    // TO TOGGLE SIDEBAR
    const openMenu = ( BtnClass ) => {

        if ( $('.SideBar').hasClass('showSidebar') )
        {
            $('.SideBar').removeClass('showSidebar');
            $('.Topbar .las').removeClass('la-times');
            $('.Topbar .las').addClass(BtnClass);
        }else
        {
            $('.SideBar').addClass('showSidebar');
            $('.Topbar .las').removeClass(BtnClass);
            $('.Topbar .las').addClass('la-times');
        }

    }

    return (
        <div className="Topbar">
            <div className="d-flex align-items-center">
                <div className="Search">
                    {/* SEARCH ICON */}
                    <img src={ searchIco } className="mr-2" width="20" height="20" alt="Search Icon" />
                    {/* SEARCH INPUT */}
                    <input placeholder="Tap to search" type="text" name="appSearch" className="appSearch" />
                </div>
            </div>
            <div>
                <div className="user-info">
                    {/* USER NOTIFICATIONS */}
                    <div className="Notifications">
                        {/* NOTIFICATIONS COUNT */}
                        <div className="count">1</div>
                        <img src={ bellIco } className="mr-2" width="20" height="20" alt="Bell Icon" />
                    </div>
                    <div className="user">
                        {/* USER IMAGE */}
                        <div 
                            className="userImg"
                            style={
                                {
                                    backgroundImage: "url('https://miro.medium.com/max/1400/0*MYd28f8vpLiE34ij.jpeg')"
                                }
                            }
                        ></div>
                        <p className="mb-0 font-weight-bold">User</p>
                        <img src={ downIco } className="mr-2" width="20" height="20" alt="Down Arrow Icon" />
                    </div>
                </div>
            </div>
            <div>
                <i onClick={ () => openMenu('la-ellipsis-v') } className="las la-ellipsis-v"></i>
            </div>
        </div>
    )
}

export default Topbar;
